// 切換底圖
let changeBtn = document.getElementById('change');
changeBtn.addEventListener("click", function (event) {
  let type = changeBtn.getAttribute('data-type');
  
  if (type == 'XYZ') {

    const key = 'xa04PgV20nbT2LNRdFnA';
    const attributions =
    '<a href="https://www.maptiler.com/copyright/" target="_blank">&copy; MapTiler</a> ' +
    '<a href="https://www.openstreetmap.org/copyright" target="_blank">&copy; OpenStreetMap contributors</a>';

    source = new XYZ({
      url: 'https://api.maptiler.com/maps/streets/{z}/{x}/{y}.png?key=' + key,
      attributions: attributions,
      crossOrigin: 'anonymous',
      tileSize: 512,
    });
    layer.setSource(source);
    changeBtn.setAttribute('data-type', 'OSM');
  } else {

    source = new OSM();

    layer.setSource(source);
    changeBtn.setAttribute('data-type', 'XYZ');
  }
  
});

// 畫圖
let drawBtn = document.getElementById('draw');
drawBtn.addEventListener("click", function (event) {
  let status = drawBtn.getAttribute('data-status');
  drawBtn.setAttribute('data-status', 'true');
  
  if (status ) {
    source = new VectorSource();
    layer = new VectorLayer({
        source: source,
    });
    map.addLayer(layer);
    // map.addInteraction(
    //     new DragAndDrop({
    //         source: source,
    //         formatConstructors: [GeoJSON],
    //     })
    // );
    map.addInteraction(
      new Draw({
          type: 'Polygon',
          source: source,
      })
    );
  } else {

  }
  
});



// 清除
let clear = document.getElementById('clear');
clear.addEventListener('click', function () {
    source.clear();
});



